/* global Office, Excel */

/**
 * Simple Excel Chatbot Add-in
 * - Finds "InSync API" sheet
 * - Sends data to backend for embeddings
 * - Queries backend with RAG
 * - Displays answers and charts
 */

// Configuration
const BACKEND_URL = 'https://apparel-ended-talks-far.trycloudflare.com';
const SHEET_NAME = 'InSync API';

// State
let isReady = false;
let isProcessing = false;
let embeddingsLoaded = false;  // Track if embeddings are already loaded

// Conversation Memory State
let sessionId = null;
let conversationHistory = [];  // {role: 'user'|'assistant', content: string, timestamp: number}

// Initialize when Office is ready
Office.onReady((info) => {
    if (info.host === Office.HostType.Excel) {
        console.log('Office Add-in initialized');
        initializeAddin();
    }
});

/**
 * Initialize the add-in
 */
async function initializeAddin() {
    // Initialize session
    initializeSession();

    // Set up event listeners
    document.getElementById('send-btn').addEventListener('click', handleSendMessage);
    document.getElementById('user-input').addEventListener('keypress', (e) => {
        if (e.key === 'Enter' && !isProcessing) {
            handleSendMessage();
        }
    });
    document.getElementById('clear-chat-btn').addEventListener('click', clearConversation);

    // Show welcome message
    addMessage('Welcome! I\'ll help you analyze your Excel data.', 'assistant');

    // Load InSync API sheet
    await loadInSyncAPISheet();
}

/**
 * Initialize or restore conversation session
 */
function initializeSession() {
    // Try to restore session from sessionStorage
    const savedSessionId = sessionStorage.getItem('chatSessionId');
    const savedHistory = sessionStorage.getItem('conversationHistory');

    if (savedSessionId && savedHistory) {
        sessionId = savedSessionId;
        try {
            conversationHistory = JSON.parse(savedHistory);
            console.log(`Restored session: ${sessionId} with ${conversationHistory.length} messages`);
            updateContextIndicator();

            // Restore messages in UI (skip welcome message)
            conversationHistory.forEach(msg => {
                if (msg.content && msg.role) {
                    addMessage(msg.content, msg.role);
                }
            });
        } catch (e) {
            console.error('Failed to restore history:', e);
            createNewSession();
        }
    } else {
        createNewSession();
    }
}

/**
 * Create a new conversation session
 */
function createNewSession() {
    sessionId = generateUUID();
    conversationHistory = [];
    sessionStorage.setItem('chatSessionId', sessionId);
    sessionStorage.setItem('conversationHistory', JSON.stringify([]));
    console.log(`Created new session: ${sessionId}`);
    updateContextIndicator();
}

/**
 * Generate a UUID for session identification
 */
function generateUUID() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        const r = Math.random() * 16 | 0;
        const v = c === 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}

/**
 * Sanitize error messages to hide internal system details
 */
function sanitizeErrorMessage(errorMessage) {
    if (!errorMessage || typeof errorMessage !== 'string') {
        return 'Processing error occurred. Please try again.';
    }

    const message = errorMessage.toLowerCase();

    // Check for Gemini-specific errors
    if (message.includes('gemini') ||
        message.includes('google') ||
        message.includes('generative') ||
        message.includes('model') ||
        message.includes('api key') ||
        message.includes('quota') ||
        message.includes('rate limit') ||
        message.includes('safety') ||
        message.includes('blocked') ||
        message.includes('function call') ||
        message.includes('tool')) {
        return 'InSync AI processing error. Please try again later or rephrasing your question.';
    }

    // Check for backend/server errors
    if (message.includes('server') ||
        message.includes('backend') ||
        message.includes('fastapi') ||
        message.includes('internal server error') ||
        message.includes('500') ||
        message.includes('503') ||
        message.includes('timeout')) {
        return 'Service temporarily unavailable. Please try again in a moment.';
    }

    // Check for network/connection errors
    if (message.includes('network') ||
        message.includes('connection') ||
        message.includes('fetch') ||
        message.includes('cors') ||
        message.includes('refused')) {
        return 'Connection error. Please check your network and try again.';
    }

    // Check for authentication/permission errors
    if (message.includes('unauthorized') ||
        message.includes('forbidden') ||
        message.includes('401') ||
        message.includes('403')) {
        return 'Access denied. Please contact support.';
    }

    // Check for data/parsing errors
    if (message.includes('json') ||
        message.includes('parse') ||
        message.includes('syntax') ||
        message.includes('invalid')) {
        return 'Data processing error. Please try again.';
    }

    // For any other specific technical terms, use generic message
    if (message.includes('exception') ||
        message.includes('traceback') ||
        message.includes('error:') ||
        message.includes('failed to')) {
        return 'Processing error occurred. Please try again.';
    }

    // If error message is very long (likely technical), shorten it
    if (errorMessage.length > 100) {
        return 'InSync AI encountered an issue. Please try again later or rephrasing your question.';
    }

    // Return original message if it seems user-friendly
    return errorMessage;
}

/**
 * Update context indicator in UI
 */
function updateContextIndicator() {
    const indicator = document.getElementById('context-indicator');
    if (indicator) {
        const messageCount = conversationHistory.length;
        if (messageCount > 0) {
            indicator.textContent = `${messageCount} messages`;
            indicator.style.display = 'block';
        } else {
            indicator.style.display = 'none';
        }
    }
}

/**
 * Save conversation history to sessionStorage
 */
function saveConversationHistory() {
    try {
        sessionStorage.setItem('conversationHistory', JSON.stringify(conversationHistory));
    } catch (e) {
        console.error('Failed to save conversation history:', e);
    }
}

/**
 * Clear conversation and start fresh (keeps embeddings)
 */
async function clearConversation() {
    try {
        updateStatus('Clearing chat history...', 'info');

        // Reset server session (clears chat, keeps embeddings)
        try {
            const response = await fetch(`${BACKEND_URL}/api/session/reset`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ sessionId: sessionId })
            });
            const result = await response.json();
            console.log('Session reset:', result.message);
            console.log('Embeddings preserved:', result.embeddingsPreserved);
        } catch (e) {
            console.warn('Could not reset server session:', e);
        }

        // Clear UI
        const container = document.getElementById('chat-container');
        container.innerHTML = '';

        // Clear local conversation history (but keep sessionId - same session continues)
        conversationHistory = [];
        saveConversationHistory();
        updateContextIndicator();

        // Note: embeddings are still loaded, no need to reload

        // Show welcome message
        addMessage('Chat history cleared. Embeddings preserved. How can I help you?', 'assistant');
        updateStatus('Ready - Chat cleared', 'success');

    } catch (error) {
        console.error('Error clearing conversation:', error);
        const sanitizedError = sanitizeErrorMessage(error.message);
        updateStatus(`Error: ${sanitizedError}`, 'error');
    }
}

/**
 * Load InSync API sheet and create embeddings
 */
async function loadInSyncAPISheet() {
    // Skip if embeddings are already loaded
    if (embeddingsLoaded) {
        console.log('Embeddings already loaded, skipping...');
        return;
    }

    try {
        updateStatus('Looking for InSync API sheet...', 'info');

        await Excel.run(async (context) => {
            // Get the worksheet
            const sheets = context.workbook.worksheets;
            sheets.load('items/name');
            await context.sync();

            // Find InSync API sheet
            const inSyncSheet = sheets.items.find(sheet => sheet.name === SHEET_NAME);

            if (!inSyncSheet) {
                throw new Error(`Sheet "${SHEET_NAME}" not found. Please ensure you have a sheet named "${SHEET_NAME}".`);
            }

            updateStatus('Reading InSync API sheet...', 'info');

            // Get the used range
            const usedRange = inSyncSheet.getUsedRange();
            usedRange.load(['values', 'address']);
            await context.sync();

            const data = usedRange.values;
            const address = usedRange.address;

            console.log(`Read ${data.length} rows from ${SHEET_NAME}`);

            // Extract headers (row 7, index 6)
            const headers = data[6] || [];

            // Send to backend to create embeddings (link to session)
            updateStatus('Creating embeddings...', 'info');

            const response = await fetch(`${BACKEND_URL}/api/embeddings/create-from-data?session_id=${sessionId}`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    worksheetName: SHEET_NAME,
                    data: data,
                    headers: headers,
                    metadata: {
                        totalRows: data.length,
                        totalCols: headers.length,
                        address: address
                    }
                })
            });

            if (!response.ok) {
                const error = await response.json();
                const errorMessage = error.detail || 'Failed to create embeddings';
                throw new Error(sanitizeErrorMessage(errorMessage));
            }

            const result = await response.json();
            console.log('Embeddings created:', result);

            // Success!
            isReady = true;
            embeddingsLoaded = true;  // Mark embeddings as loaded
            updateStatus(`Ready! Loaded ${result.lineItemCount} metrics, ${result.columnCount} periods`, 'success');
            enableInput();
            addMessage(`I've loaded your InSync API data with ${result.lineItemCount} line items and ${result.columnCount} time periods. What would you like to know?`, 'assistant');
        });

    } catch (error) {
        console.error('Error loading sheet:', error);
        updateStatus('Failed to load data', 'error');
        const sanitizedError = sanitizeErrorMessage(error.message);
        addMessage(`Error: ${sanitizedError}`, 'error');
    }
}

/**
 * Handle send message
 */
async function handleSendMessage() {
    if (isProcessing || !isReady) return;

    const input = document.getElementById('user-input');
    const message = input.value.trim();

    if (!message) return;

    // Clear input
    input.value = '';

    // Add user message
    addMessage(message, 'user');

    // Add to conversation history
    const userMessage = {
        role: 'user',
        content: message,
        timestamp: Date.now()
    };
    conversationHistory.push(userMessage);
    saveConversationHistory();
    updateContextIndicator();

    // Process
    isProcessing = true;
    disableInput();
    showLoading(true);
    updateStatus('Analyzing...', 'info');

    try {
        // Get chart toggle state
        const enableCharts = document.getElementById('chart-toggle').checked;
        console.log(`ðŸ“Š Charts toggle state: ${enableCharts}`);

        // Call backend API with sessionId
        const response = await fetch(`${BACKEND_URL}/api/chat`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                question: message,
                sessionId: sessionId,  // Include session ID
                excelData: {
                    worksheetName: SHEET_NAME,
                    data: [],
                    headers: []
                },
                conversationHistory: conversationHistory,  // Send full history for context
                enableCharts: enableCharts
            })
        });

        if (!response.ok) {
            const error = await response.json();
            const errorMessage = error.detail || 'Request failed';
            throw new Error(sanitizeErrorMessage(errorMessage));
        }

        const result = await response.json();
        console.log('Response:', result);

        // Display answer
        addMessage(result.answer, 'assistant');

        // Add assistant response to conversation history
        const assistantMessage = {
            role: 'assistant',
            content: result.answer,
            timestamp: Date.now(),
            hasChart: result.hasChart || false
        };
        conversationHistory.push(assistantMessage);
        saveConversationHistory();
        updateContextIndicator();

        // Display chart if available
        if (result.hasChart && result.chartHtml) {
            addChartMessage(result.chartHtml);
        }

        // Update context indicator if server returned contextLength
        if (result.contextLength !== undefined) {
            console.log(`Server context length: ${result.contextLength} messages`);
        }

        updateStatus('Ready', 'success');

    } catch (error) {
        console.error('Error:', error);
        const sanitizedError = sanitizeErrorMessage(error.message);
        addMessage(`Error: ${sanitizedError}`, 'error');
        updateStatus('Error occurred', 'error');
    } finally {
        isProcessing = false;
        enableInput();
        showLoading(false);
        input.focus();
    }
}

/**
 * Parse markdown to HTML
 */
function parseMarkdown(text) {
    // Convert markdown to HTML
    let html = text;

    // Code blocks (```code```)
    html = html.replace(/```(\w+)?\n([\s\S]*?)```/g, (match, lang, code) => {
        return `<pre><code class="language-${lang || 'text'}">${escapeHtml(code.trim())}</code></pre>`;
    });

    // Inline code (`code`)
    html = html.replace(/`([^`]+)`/g, '<code>$1</code>');

    // Bold (**text** or __text__)
    html = html.replace(/\*\*([^*]+?)\*\*/g, '<strong>$1</strong>');
    html = html.replace(/__([^_]+?)__/g, '<strong>$1</strong>');

    // Italic (*text* or _text_) - must have space or word boundary before/after
    html = html.replace(/(\s|^)\*([^\s*][^*]*?)\*(\s|$|[.,!?;:])/g, '$1<em>$2</em>$3');
    html = html.replace(/(\s|^)_([^\s_][^_]*?)_(\s|$|[.,!?;:])/g, '$1<em>$2</em>$3');

    // Headers
    html = html.replace(/^### (.+)$/gm, '<h3>$1</h3>');
    html = html.replace(/^## (.+)$/gm, '<h2>$1</h2>');
    html = html.replace(/^# (.+)$/gm, '<h1>$1</h1>');

    // Lists (unordered)
    html = html.replace(/^\* (.+)$/gm, '<li>$1</li>');
    html = html.replace(/^- (.+)$/gm, '<li>$1</li>');

    // Wrap consecutive <li> in <ul>
    html = html.replace(/(<li>.*<\/li>\n?)+/g, (match) => {
        return `<ul>${match}</ul>`;
    });

    // Lists (ordered)
    html = html.replace(/^\d+\. (.+)$/gm, '<li>$1</li>');

    // Links [text](url)
    html = html.replace(/\[([^\]]+)\]\(([^)]+)\)/g, '<a href="$2" target="_blank">$1</a>');

    // Line breaks
    html = html.replace(/\n\n/g, '</p><p>');
    html = html.replace(/\n/g, '<br>');

    // Wrap in paragraph
    html = `<p>${html}</p>`;

    // Clean up empty paragraphs
    html = html.replace(/<p><\/p>/g, '');
    html = html.replace(/<p>(<h[123]>)/g, '$1');
    html = html.replace(/(<\/h[123]>)<\/p>/g, '$1');
    html = html.replace(/<p>(<pre>)/g, '$1');
    html = html.replace(/(<\/pre>)<\/p>/g, '$1');
    html = html.replace(/<p>(<ul>)/g, '$1');
    html = html.replace(/(<\/ul>)<\/p>/g, '$1');

    return html;
}

/**
 * Escape HTML special characters
 */
function escapeHtml(text) {
    const div = document.createElement('div');
    div.textContent = text;
    return div.innerHTML;
}

/**
 * Add a message to the chat
 */
function addMessage(text, type) {
    const container = document.getElementById('chat-container');
    const messageDiv = document.createElement('div');
    messageDiv.className = `message message-${type}`;

    const contentDiv = document.createElement('div');
    contentDiv.className = 'message-content';

    // Parse markdown for assistant messages
    if (type === 'assistant') {
        contentDiv.innerHTML = parseMarkdown(text);
    } else {
        contentDiv.textContent = text;
    }

    messageDiv.appendChild(contentDiv);
    container.appendChild(messageDiv);

    // Scroll to bottom
    container.scrollTop = container.scrollHeight;
}

/**
 * Add a chart message
 */
function addChartMessage(chartHtml) {
    const container = document.getElementById('chat-container');
    const messageDiv = document.createElement('div');
    messageDiv.className = 'message message-chart';

    const chartDiv = document.createElement('div');
    chartDiv.className = 'chart-container';

    // Generate a unique ID for this chart to avoid conflicts
    const uniqueId = 'plotly-chart-' + Date.now() + '-' + Math.random().toString(36).substr(2, 9);

    // Extract just the div content from the full HTML if needed
    // Plotly returns full HTML page, we need to extract the chart div
    try {
        const parser = new DOMParser();
        const doc = parser.parseFromString(chartHtml, 'text/html');

        // Find the plotly chart div
        let plotlyDiv = doc.querySelector('div[id^="plotly-chart"]') || doc.querySelector('div');

        if (plotlyDiv) {
            // Clone the div
            plotlyDiv = plotlyDiv.cloneNode(true);
            
            // Update the ID to be unique
            plotlyDiv.id = uniqueId;

            chartDiv.appendChild(plotlyDiv);

            // Also copy and update scripts (Plotly needs them, and they reference the old ID)
            const scripts = doc.querySelectorAll('script');
            scripts.forEach(script => {
                let scriptContent = script.textContent || '';
                const newScript = document.createElement('script');
                
                if (script.src) {
                    newScript.src = script.src;
                } else {
                    // Replace references to the old ID in inline script content
                    const oldId = 'plotly-chart'; // Assuming this is the fixed ID from Python
                    scriptContent = scriptContent.replace(new RegExp(oldId, 'g'), uniqueId);
                    newScript.textContent = scriptContent;
                }
                
                chartDiv.appendChild(newScript);
                console.log("Added script for chart ID:", uniqueId);
            });
        } else {
            // Fallback: just insert as-is (but still update if possible)
            chartDiv.innerHTML = chartHtml.replace(/plotly-chart/g, uniqueId);
            console.log("Plotly div not found, inserted raw HTML with updated ID:", uniqueId);
        }
    } catch (error) {
        console.error('Error parsing chart HTML:', error);
        // Fallback: just insert as-is with ID replacement
        chartDiv.innerHTML = chartHtml.replace(/plotly-chart/g, uniqueId);
    }

    messageDiv.appendChild(chartDiv);
    container.appendChild(messageDiv);

    // Scroll to bottom
    container.scrollTop = container.scrollHeight;
}

/**
 * Update status bar
 */
function updateStatus(text, type) {
    const status = document.getElementById('status');
    status.textContent = text;
    status.className = `status status-${type}`;
}

/**
 * Enable/disable input
 */
function enableInput() {
    document.getElementById('user-input').disabled = false;
    document.getElementById('send-btn').disabled = false;
}

function disableInput() {
    document.getElementById('user-input').disabled = true;
    document.getElementById('send-btn').disabled = true;
}

/**
 * Show/hide loading indicator
 */
function showLoading(show) {
    const loading = document.getElementById('loading');
    loading.style.display = show ? 'flex' : 'none';
}
